import m from "mithril";

export default class SearchList {
    view(vnode) {
        const attrs = vnode.attrs;

        const cards = attrs.cards;
        const onadd = attrs.onadd;
        return (
            <div id = "cardList">
            <ul>
                {cards.map(
                    (card, i) => (
                        <li>
                            <button onclick={() => onadd(card)}>{card.name}</button>
                        </li>)
                )}
            </ul>
            </div>
        )
    }
}