import Immutable from "immutable";

import * as deckActions from "./deckActions";

import { produce } from "immer"

const initialState = {
    lands: Immutable.Map(),
    landsAmounts: Immutable.Map(),
    nonLands: Immutable.Map(),
    nonLandsAmounts: Immutable.Map(),
}

function _addCard(card, objects, amounts) {
    const amount = amounts.get(card.id);
    console.log(amount)
    if (amount === undefined) {
        objects.set(card.id, card);
        amounts.set(card.id, 1);
        return;
    }

    if (amount < 4) {
        amounts.set(card.id, amount + 1);
    }
}


function _removeCard(card, objects, amounts) {
    const amount = amounts.get(card.id);
    if (amount === undefined) {
        return;
    }

    if (amount === 1) {
        amounts.delete(card.id);
        objects.delete(card.id);
    }

    amounts.set(card.id, amount - 1);
}

const _isLand = (card) => card.type_line.includes("Land")

export default function deckReducer(state = initialState, action) {

    switch (action.type) {
        case deckActions.CARD_ADDED:
            return produce(state, state => {
                const card = action.payload
                _isLand(card) ? _addCard(card, state.lands, state.landsAmounts)
                    :
                    _addCard(card, state.nonLands, state.nonLandsAmounts);
                console.log(state)
            });

        case deckActions.CARD_REMOVED:
            return produce(state, state => {
                const card = action.payload
                _isLand(card) ? _removeCard(card, state.lands, state.landsAmounts)
                    :
                    _removeCard(card, state.nonLands, state.nonLandsAmounts);
            });

        default:
            return state;
    }
}