import m from "mithril";

import * as deckActions from '../state/deckActions';

export default class Deck {
    view(vnode) {
        const attrs = vnode.attrs;

        const cardObjects = attrs.cardObjects;
        const cardAmounts = attrs.cardAmounts;
        return (
            <div class="content">
                <div id="grid">

                    {Array.from(cardObjects).map(
                        (keyValue, i) => {
                            const card = keyValue[1]

                            return <div>
                                <Card card = {card}
                                amount = {cardAmounts.get(card.id)}
                                onadd = {(card)=>{deckActions.cardAdded(card)}}
                                onremove = {(card)=>{deckActions.cardRemoved(card)}}
                                ></Card>
                            </div>
                        }
                    )
                    }

                </div>
            </div>
        )
    }
}

class Card {
    view(vnode) {
        const attrs = vnode.attrs;

        const card = attrs.card;
        const amount = attrs.amount;

        const onadd = attrs.onadd;
        const onremove = attrs.onremove;

        const image = card.image_uris !== undefined ? card.image_uris.normal : ''; //TODO: handle undefined
        return (
            <>
                <img src={image} style='height: 250px;'></img>
                <div>
                    <button onclick = {()=>onremove(card)}>-</button>
                    {amount}
                    <button onclick = {()=>onadd(card)}>+</button>
                </div>
            </>
        )
    }
}