export const selectDeck = state => ({
    cards: [...state.deck.nonLands, ...state.deck.lands],
    amounts: [...state.deck.nonLandsAmounts, ...state.deck.landsAmounts]
})

const emptyColorData = [
    { color: 'W', count: 0 },
    { color: 'U', count: 0 },
    { color: 'B', count: 0 },
    { color: 'R', count: 0 },
    { color: 'G', count: 0 },
    { color: 'C', count: 0 }
]
const colorToIndex = {
    W: 0,
    U: 1,
    B: 2,
    R: 3,
    G: 4,
    C: 5
};

// TODO: RESELECT
export const selectColorData = (state) => {
    cards = state.deck.nonLands;
    result = [...emptyColorData];

    for (const keyValue of cards) {
        id = keyValue[0];
        card  = keyValue[1];
        if (card.color_indetity.empty) {
            result['C'].count += cards.nonLandsAmounts.get(id);
            break;
        }
        for (const color of card.color_indetity) {
            result[colorToIndex(color)].count += cards.nonLandsAmounts.get(id);
        }
    }
    return result;
}


